import $ from 'jquery'
import { fetch, fetchInterval } from '@/util/request'
import loader from '@/loader/base.config.js'
// 模板
import hbs from '../../templates/county/index.hbs'
// 在网终端
import InNetwork from './InNetwork.js'
// 手机数量统计
import PhoneNumber from './phoneNumber.js'
// 渲染地图
import Map from './renderMap.js'
// 运营商
import Operator from './operator.js'
// top10
import Top10 from './top10.js'
// top10 搜索
import TopSearch from './topSearch.js'
// 模块名
const COUNTY_MODULE = loader.COUNTY_MODULE

export default class County {

  /**
   *  构造函数
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  constructor() {
    // 渲染模板
    $(COUNTY_MODULE).html(hbs())
    // 实化例
    this.inNetwork = new InNetwork()
    this.phoneNumber = new PhoneNumber()
    this.map = new Map()
    this.operator = new Operator('.operator-svg')
    this.top10 = new Top10()
    this.topSearch = new TopSearch()
  } 

  /**
   *  发送请求
   *  @param    {string}  areaId 地区编码
   *  @return   {void}  
   */
  fetchData(areaId) {
    const self = this
    // 在网终端
    fetchInterval('fetchCInNetwork', {areaId}, (data) => {
      self.inNetwork.render(data)
      self.operator.render(data.operator)
    }, loader.updateTime)

    // 手机数量统计
    fetchInterval('fetchCPhoneCount', {areaId}, (data) => {
      self.phoneNumber.render(data.phoneCount)
    }, loader.updateTime)

    // top10排行 
    fetchInterval('fetchCAreaTop10', {areaId}, (data) => {
      self.top10.render(data.areaTop10)
      self.bindEvent(areaId)
    }, loader.updateTime)
  }

  /**
   *  事件绑定
   *  @param    {string}  areaId 地区编码
   *  @return   {void}   
   */
  bindEvent(areaId) {
    const self = this
    // 出渝手机top10点击查看详情
    $('.area-top').off('click', '.top-details').on('click', '.top-details', () => {
      // 获取当前选择项的类型(province, city, abroad)
      const selector = $('.area-top').find('.active')
      let type = selector.attr('type')
      fetch('fetchCAreaTopDetails', {areaId, type}, (data) => {
        // 渲染详情
        self.top10.renderDeitals(data.areaTopDetails, selector) 
        // 绑定搜索
        self.topSearch.init(areaId, type)
      })
    })
  }

  /**
   *  初始化
   *  @param    {string}  areaId 地区编码
   *  @return   {void}  
   */
  init(mapId, areaId) {
    const self = this
    // 渲染地图
    self.map.renderMap(mapId)
    // 获取地图
    self.fetchData(areaId)
  }
} 



