import $ from 'jquery'
import { CountyMap } from '@/charts/index'
import loader from '@/loader/base.config.js'
// 模块名
const COUNTY_MODULE = loader.COUNTY_MODULE

export default class renderMap {
  
  constructor() {
    this.map = new CountyMap(`${COUNTY_MODULE} .map-main`)
  }

  /**
   *  渲染地图
   *  @example: [example]
   *  @param    {string}  areaId 地区编码
   *  @return   {viod}   
   */
  renderMap(areaId) {
    this.map.render(areaId)
    // 修改提示框位置
    this.setPositin(areaId)
  }

  /**
   *  修改提示框的位置
   *  @param    {string}  areaId 地区编码
   *  @return   {viod}   
   */
  setPositin(areaId) {
    switch(areaId){
      case '500107': // 九龙坡
        $(`${COUNTY_MODULE} .area-operator`).animate({
            left: '250px',
            top: '130px'
          })
        break
      case '500227': // 璧山
        $(`${COUNTY_MODULE} .area-operator`).animate({
          left: '380px',
          top: '130px'
        })  
        break
      case '500104': // 大渡口
      case '500109': // 北碚
        $(`${COUNTY_MODULE} .area-operator`).animate({
          left: '580px',
          top: '130px'
        })    
        break
      case '500112': // 渝北区
        $(`${COUNTY_MODULE} .area-operator`).animate({
          left: '600px',
          top: '130px'
        })    
        break
      case '500241': // 秀山
        $(`${COUNTY_MODULE} .area-operator`).animate({top: '310px'})    
        break  
      case '500236': // 奉节
        $(`${COUNTY_MODULE} .area-operator`).animate({left: '420px'})    
        break   
      case '500103': // 渝中
        $(`${COUNTY_MODULE} .area-operator`).animate({
          left: '305px',
          top: '300px'
        })      
        break
      case '500113': // 巴南
        $(`${COUNTY_MODULE} .area-operator`).animate({
          top: '315px'
        })    
        break  
      case '500105': // 江北区  
        $(`${COUNTY_MODULE} .area-operator`).animate({
          top: '280px' ,
          left: '500px'
        })    
        break
      default:
        $(`${COUNTY_MODULE} .area-operator`).animate({
          left: '500px',
          top: '130px'
        })
        break
    }
  }
} 



