import './index.css'
import mockApis from './mock'
import loader from '@/loader/loader'
import Header from './scripts/common/header.js'
import Main from './scripts/main'
const main = new Main()
const header = new Header()

loader.load({
  apis: mockApis, 
  init() {
    // 用户信息
    header.render()
    // 总览页
    main.init()
  }
})



