import $ from 'jquery'
import hbs from '../../templates/main/index.hbs'
import Top10 from './top10'
import InNetwork from './InNetwork.js'
import PhoneNumber from './phoneNumber.js'
import ControlTaskCout from './ControlTaskCout.js'
import HeatMap from '../heatMap'

export default class Main {

  /**
   *  构造函数
   *  @return   {void}  
   */
  constructor() {
    $('.dynamic-main-templates').html(hbs())
    this.inNetwork = new InNetwork()
    this.phoneNumber = new PhoneNumber()
    this.top = new Top10()
    this.controlTaskCout = new ControlTaskCout()
    this.heatMap = new HeatMap()
  }  

  /**
   *  初始化
   *  @return   {void}   
   */
  init() {
    const self = this
    // 全市在网终端、在网人数
    self.inNetwork.render()
    // 在渝、出渝手机数量统计
    self.phoneNumber.render()
    // 渲染管控任务数量统计
    self.controlTaskCout.render()
    // top排行
    self.top.init()
    // 热力图
    self.heatMap.init()
  }
}



