import $ from 'jquery'
import { fetch, fetchInterval } from '@/util/request'
import { showAllName } from '../common/common.js'
import loader from '@/loader/base.config.js'
import top10Hbs from '../../templates/common/top10.hbs'
import topList from '../../templates/common/topList.hbs'
import topDeitalsList from '../../templates/common/topDeitalsList.hbs'
import topDetialsHbs from '../../templates/common/topDeitals.hbs'
// 模块名
const MAIN_MODULE = loader.MAIN_MODULE
let type = ''
let state = ''
let inType = 'province' // 用于鼠标移上去显示全部，判断显示长度
let outType = 'province' // 用于鼠标移上去显示全部，判断显示长度

export default class Top10 {
  /**
   *  构造函数
   *  @return   {object}  null
   */
  constructor() {
    // 存储请求回来的数据，一次请求回来的数据包括省份、地市级和国外三种数据
    this.inTopListData = {}
    this.outTopListData = {}
    // 渲染Top10模板
    $(`${MAIN_MODULE} .in-cq-top`).html(top10Hbs())
    $(`${MAIN_MODULE} .out-cq-top`).html(top10Hbs())
    // 初始化时候修改出渝Top10标题
    $(`${MAIN_MODULE} .out-cq-top .yu-type`).text('出渝')
    $('.top-detials-dialog').html(topDetialsHbs())
  }
 
  /**
   *  渲染top10列表
   *  @param    {string}  selector 容器元素选择
   *  @param    {array}  data 数据
   *  @return   {object}  null
   */
  renderTopList(selector, data) {
    data.length = data.length
    $(`${selector} .top10-list`).html(topList(data))
  }

  /**
   *  发送ajax请求，获取在渝数据
   *  @return   {object}  null
   */
  fetchInData() {
    const self = this
    const topC = $('.in-cq-top .top10-container')
    // 10分钟更新
    fetchInterval('fetchInYuTop', (data) => {
      self.inTopListData = data.inYuTop10
      self.renderTopList('.in-cq-top', self.inTopListData.province)
      $('.in-cq-top .nav').eq(0).addClass('active').siblings()
        .removeClass('active')
      // 添加一个class，用于区分是否要点击事件
      let isClass = topC.hasClass('in-list-container')
      if(!isClass){
        topC.addClass('in-list-container')
      }  
    }, loader.updateTime)
  }
 
  /**
   *  发送ajax请求，获取出渝数据
   *  @return   {object}  null
   */
  fetchOutData() {
    const self = this
    fetch('fetchOutYuTop',
      data => {
        self.outTopListData = data.outYuTop10
        self.renderTopList('.out-cq-top', self.outTopListData.province)
      }
    )
  }
 
  /**
   *  绑定事件
   *  @return   {object}  null
   */
  bindEvent() {
    const self = this
    // 在渝手机top10点击切换
    $('.in-cq-top').on('click', '.nav', (evt) => {
      const $this = $(evt.target)
      inType = $this.attr('type')
      $('.in-cq-top .top10-list').removeClass('city-list abroad-list province-list')
        .addClass(`${inType}-list`)
      self.switchNav('.in-cq-top', $this, '在渝')
    })

    // 出渝手机top10点击切换
    $('.out-cq-top').on('click', '.nav', (evt) => {
      const $this = $(evt.target)
      outType = $this.attr('type')
      $('.out-cq-top .top10-list').removeClass('city-list abroad-list province-list')
        .addClass(`${outType}-list`)
      self.switchNav('.out-cq-top', $this, '出渝')
    })

    // 鼠标移上去
    $('.in-cq-top .top10-list').on('mouseover', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, inType)
    })

    $('.out-cq-top .top10-list').on('mouseover', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, outType)
    })
     
    $('.in-cq-top .top10-list').on('mousemove', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, inType)
    })

    $('.out-cq-top .top10-list').on('mousemove', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, outType)
    })

    // 鼠标离开
    $('.top10-list').on('mouseout', 'li a:nth-child(1)', () => {
      $('.show-all-name').hide().text('')
    })

    // 渲染详情
    self.renderDetails()

    // 点击搜索
    $('.search-btn').on('click', () => {
      $('.search-list').scrollTop(0)
      self.renderSearch()
    })

    // 获取input框的keyCode
    $('.search-text').on('keypress', (evt) => {
      let keyCode = evt.keyCode
      // 回车
      if(keyCode === 13){
        $('.search-list').scrollTop(0)
        self.renderSearch()
      }
    })
  }

  /**
   *  渲染搜索
   *  @return   {object}  null
   */
  renderSearch() {
    let text = $('.search-text').val()
    $('.search-text').attr('value', text)
    // 过滤空格
    text = $.trim(text)
    if(text === '') {
      text = 'all' // 为空搜索全部
    }
    fetch('fetchSearch', {state, type, text}, (data) => {
      // 渲染搜索出来的数据
      $('.search-list').html(topDeitalsList(data.searchList))
    })
  }
  /**
   *  渲染详情
   *  @return   {object}  null
   */
  renderDetails() {
    const topC = $('.top-detials-dialog .list-container')
    // 在渝手机top10点击查看详情
    $('.in-cq-top').on('click', '.top-details', () => {
      // 获取当前选择项的类型(province, city, abroad)
      let selector = $('.in-cq-top').find('.active')
      type = selector.attr('type')
      // list添加样式，调整宽度
      $('.search-list').removeClass('city-list abroad-list province-list')
        .addClass(`${type}-list`)
      let typezn = selector.attr('type-zn') 
      $('.detials-content .current-type').text(typezn)
      $('.detials-content .yu-type').text('在渝')
      state = 'in'
      // 发送请求
      fetch('fetchInYuTopDetails', {type}, (data) => {
        if(typezn === '省级') {
          typezn = '省级名称'
        }
        $('.search-text').val('').attr('placeholder', `请输入${typezn}进行排行搜索`)
        $('.top-detials-dialog').fadeIn().find('.search-list')
          .html(topDeitalsList(data.inYuTopDetails))
        // 添加一个class，用于区分是否要点击事件
        let isClass = topC.hasClass('in-list-container')
        if(!isClass){
          topC.addClass('in-list-container')
        } 
      })
    })

    // 出渝手机top10点击查看详情
    $('.out-cq-top').on('click', '.top-details', () => {
      // 获取当前选择项的类型(province, city, abroad)
      let selector = $('.out-cq-top').find('.active')
      type = selector.attr('type')
      // list添加样式，调整宽度
      $('.search-list').removeClass('city-list abroad-list province-list')
        .addClass(`${type}-list`)
      let typezn = selector.attr('type-zn') 
      $('.detials-content .current-type').text(typezn)
      $('.detials-content .yu-type').text('出渝')
      state = 'out'
      fetch('fetchOutYuTopDetails', {type}, (data) => {
        if(typezn === '省级') {
          typezn = '省级名称'
        }
        $('.search-text').val('').attr('placeholder', `请输入${typezn}进行排行搜索`)
        $('.top-detials-dialog').fadeIn().find('.search-list')
          .html(topDeitalsList(data.outYuTopDetails))
          
        // 移除class，添加点击事件  
        topC.removeClass('in-list-container')    
      })
    })

    // 鼠标移上去
    $('.search-list').on('mouseover', 'li a:nth-child(2)', (evt) => {
      showAllName(evt, 2, type)
    })

    // 鼠标移动
    $('.search-list').on('mousemove', 'li a:nth-child(2)', (evt) => {
      showAllName(evt, 2, type)
    })

    // 鼠标离开
    $('.search-list').on('mouseout', 'li a:nth-child(2)', () => {
      $('.show-all-name').hide().text('')
    })

    // 点击关闭
    $('.top-detials-dialog').on('click', '.close', () => {
      $('.top-detials-dialog').fadeOut()
    })
  }

  /**
   *  切换导航
   *  @param    {string}  selector   元素选择器
   *  @param    {object}  source 来源事件
   *  @param    {string}  text   文字(在渝、出渝)
   *  @return   {object}  null
   */
  switchNav(selector, source, text) {
    const self = this
    const $this = source
    const current = $this.attr('type')
    const currentZN = $this.attr('type-zn')
    $this.siblings().removeClass('active')
    $this.addClass('active')
    $(`${selector} .yu-type`).text(text)
    $(`${selector} .current-type`).text(currentZN)
    if(text === '在渝') {
      self.renderTopList(selector, self.inTopListData[current])
    }else {
      self.renderTopList(selector, self.outTopListData[current])
    }
  }
 
  /**
   *  top list初始化方法
   *  @return   {object}  null
   */
  init() {
    const self = this
    self.fetchInData()
    self.fetchOutData()
    self.bindEvent()
  }
}



