export default {
  url: '/main/controlAreaDetails',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'controlAreaDetails|20-150': [
        {
          'name': '@pick(["企事业单位", "重要景区", "重要商圈", "宗教场所", "交通枢纽", "汽车站"])',
          'value|1-3000': 1
        }
      ]
    }
  }
}




