import $ from 'jquery'
import loader from '@/loader/base.config.js'
import { showAllName } from '../common/common.js'
import top10Hbs from '../../templates/common/top10.hbs'
import topList from '../../templates/common/topList.hbs'
import topDeitalsList from '../../templates/common/topDeitalsList.hbs'
import topDetialsHbs from '../../templates/common/topDeitals.hbs'
// 模块名
const COUNTY_MODULE = loader.COUNTY_MODULE
 
export default class Top10 {
  /**
   *  构造函数
   *  @return   {object}  null
   */
  constructor() {
    // 存储请求回来的数据，一次请求回来的数据包括省份、地市级和国外三种数据
    this.inTopListData = {}
    this.outTopListData = {}
    // 渲染Top10模板
    $(`${COUNTY_MODULE} .area-top`).html(top10Hbs())
    // 初始化时候修改出渝Top10标题
    $(`${COUNTY_MODULE} .area-top .yu-type`).text('出渝')
    $('.top-detials-dialog').html(topDetialsHbs())
  }
  
  /**
   *  渲染
   *  @example: [example]
   *  @param    {[type]}  data [description]
   *  @return   {void}   
   */
  render(data) {
    const self = this
    // 默认省级排行
    $(`${COUNTY_MODULE} .top10-list`).html(topList(data.province))
    self.bindEvent(data)
  }

  /**
   *  渲染全部
   *  @param    {array}   data 数据
   *  @param    {object}  selector 当前被选种的类型
   *  @return   {void}   
   */
  renderDeitals(data, selector) {
    let areaName = $(`${COUNTY_MODULE} .yu-type`).text() 
    // 当前排行类型
    let typezn = selector.attr('type-zn') 
    if(typezn === '省级') {
      typezn = '省级名称'
    }
    $('.search-text').val('').attr('placeholder', `请输入${typezn}进行排行搜索`)
    $('.detials-content .current-type').text(typezn)
    // 渲染列表
    $('.top-detials-dialog').fadeIn().find('.search-list')
      .html(topDeitalsList(data))
    // 改变标题(地区名)  
    $('.top-detials-dialog').find('.yu-type').text(areaName)  
  }

  /**
   *  绑定事件
   *  @param    {object}  data 数据
   *  @return   {void}  
   */
  bindEvent(data) {
    // 手机top10点击切换
    let type = null
    $(`${COUNTY_MODULE} .area-top`).on('click', '.nav', (evt) => {
      const $this = $(evt.target)
      type = $this.attr('type')
      let typezn = $this.attr('type-zn') 
      // 样式class重新添加样式
      $('.area-top .top10-list').removeClass('city-list abroad-list province-list')
        .addClass(`${type}-list`)
      $this.addClass('active').siblings().removeClass('active')
      // 标题替换
      $('.area-top .current-type').text(typezn)
      $(`${COUNTY_MODULE} .top10-list`).html(topList(data[type]))
    })

    // 鼠标移上去
    $('.area-top .top10-list').on('mouseover', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, type) // 有省略号的名称显示全
    })
    // 鼠标移动
    $('.area-top .top10-list').on('mousemove', 'li a:nth-child(1)', (evt) => {
      showAllName(evt, 1, type) // 有省略号的名称显示全
    })
  }
}



