
export default {
  url: '/county/areaTop10/:areaId',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'areaTop10': {
        'province|10': [
          {
            'name': '@province',
            'value|1-9999999': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'city|10': [
          {
            'name|+1': ['成都市(四川省)', '上海市(上海市)', '广安市(四川省)', '北京市(北京市', 
              '深圳市(广东省)', '达州市(四川省)', '泸州市(四川省)', '广州市(广东省)', '遵义市(贵州省)', 
              '东莞市(广东省)'],
            'value|1-100000': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["俄罗斯", "美国", "墨西哥", "菲律宾", "巴西"])',
            'value|1-9999999': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ]
      }
    } 
  }
}


