import $ from 'jquery'
import { fetch } from '@/util/request'
import loader from '@/loader/base.config.js'
import hbs from '../../templates/heatMap/index.hbs'
// 数字滚动
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'
import { AreaMap, MainMap } from '@/charts/index'

const selector = '.heatmap-content'
const main = '.main-map' // 主城区
const area = '.area-map' // 重庆市

export default class HeatMap {

  /**
   *  构造函数
   *  @return   {void}   
   */
  constructor() {
    $('.heatmap-dialog').html(hbs())
    this.mainMap = new MainMap('.main-map')
    this.areaMap = new AreaMap('.area-map')
    this.interval = null
  }

  /**
   *  渲染数据
   *  @param    {object}  data 地图数据
   *  @param    {string}  curArea 当前地区名
   *  @return   {void}   
   */
  render(data, curArea) {
    const self = this
    setWidth(`${selector} .totals`, data.total, 68)
    self.numberRoll(data.total)
    // 图例
    let legend = `<span>> ${data.areaRange[0]}</span> <span>< ${data.areaRange[1]}</span>`
    $('.legend').html(legend)
    $(`${selector} .county-name`).text(curArea)
    let allArea = [...data.areaMap, ...data.mainMap]
    // 保存value求最大值
    let dataset = []
    allArea.map(d => dataset.push(d.value))
    let max = Math.max(...dataset)
    allArea.curArea = curArea
    allArea.max = max
    self.areaMap.renderHeatMap(allArea)
    // 主城区地图数据
    self.mainMap.renderHeatMap(allArea)
  }

  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {void} 
   */
  numberRoll(data) {  
    const self = this 
    // 在网终端 数字滚动实例化
    const roll = new DigitRoll({
      container: `${selector} .totals .num-roll`
    })
    // 在网终端
    roll.roll(data)
    // 变化范围
    let range = 10
    // 清除定时切换
    clearInterval(self.interval)
    // 定时滚动数字
    self.interval = setInterval(() => {
      // 在网终端 (!=0 || null || undefined)
      if(data){
        // 真实值1-10范围内随机加减
        let rollData = data - range + randomNumber(1, 10)
        roll.roll(rollData)
      }     
    }, loader.aniTime) 
  }

  /**
   *  事件绑定
   *  @return   {void}   
   */
  bindEvent() {
    const self = this
    let heatMapData = null
    
    // 在渝点击事件
    $(document).on('click', '.in-list-container .name', (evt) => {
      const $this = $(evt.target)
      let areaId = $this.parents().attr('areaId') 
      let curArea = $this.text().slice(0, 3)
      // let areaId = '500234'
      // let curArea = '四川省'
      fetch('fetchMap', {areaId}, (data) => {
        heatMapData = data.heatMap
        self.render(heatMapData, curArea)
        // 默认显示重庆市地图
        self.showArea(heatMapData.areaRange)
        // 显示弹窗
        $('.heatmap-dialog').fadeIn(200)
      })
    })
    // 点击关闭
    $('.heatmap-dialog').on('click', '.close', () => {
      $('.heatmap-dialog').fadeOut()
    })

    // 点击主城区(左上角)小地图
    $(`${selector} .main-btn`).on('click', () => {
      self.showMain(heatMapData.mainRange)
      // 移出提示框
      $('.charts-tooltip').remove()
    })

    // 点击重庆地图（左上角小地图）
    $(`${selector} .area-btn`).on('click', () => {
      self.showArea(heatMapData.areaRange)
      // 移出提示框
      $('.charts-tooltip').remove()
    })
  }

  /**
   *  显示主城区地图
   *  @return   {void}  
   */
  showMain() {
    // 修改样式
    $(area).find('.heatmap-canvas').css('left', '65px')
    $(main).css({
      transform: 'scale(1, 1)',
      left: '1037px',
      top: '174px',
      'z-index': 2
    })
    // 大地图(重庆市)缩放
    $(area).css({
      transform: 'scale(0.32, 0.29)',
      top: '-402px',
      left: '234px',
      'z-index': 3
    })
    $(`${main} svg`).show()
    $(`${area} svg`).hide()
    $(`${area} .area-names-svg`).show()
    $('.area-small').show()
    $('.main-small').hide()
    $('.main-btn').hide()
    $('.area-btn').show()
  }

  /**
   *  显示重庆市地图
   *  @return   {void}  
   */
  showArea() {
    // 修改样式
    $(area).find('.heatmap-canvas').css('left', 0)
    $(area).css({
      transform: 'scale(1, 1)',
      left: '710px',
      top: '40px',
      'z-index': 2
    })
    // 主城地图缩小
    $(main).css({
      transform: 'scale(0.52, 0.45)',
      left: '564px',
      top: '-341px',
      'z-index': 3
    })
    $(`${main} .area-names-svg`).hide()
    $(`${area} svg`).show() 
    $(`${main} svg`).hide()
    $('.main-small').show()
    $('.area-small').hide()
    $('.main-btn').show()
    $('.area-btn').hide()
  }

  /**
   *  初始化
   *  @return   {[type]}  [description]
   */
  init() {
    const self = this
    self.bindEvent()
  }
}



