export default {
  url: '/county/phoneCount/:areaId',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'phoneCount': {
        'local|1-1000000000': 1,
        'nonlocal|1-10000000': 1,
        'abroad|1-100000': 1
      }
    }
  }
}



