import $ from 'jquery'
import loader from '@/loader/base.config.js'
import areaHbs from '../../templates/common/areaList.hbs'
import hbs from '../../templates/common/header.hbs'
import { fetch, fetchSocket } from '@/util/request'
import userHbs from '../../templates/common/user.hbs'
import County from '../county'

// 模块名
const COUNTY_MODULE = loader.COUNTY_MODULE // 区县
const MAIN_MODULE = loader.MAIN_MODULE // 主城
// 实例化区县
const county = new County()
let areaNams = null

export default class Header {
  
  /**
   *  构造函数
   *  @return   {object}  null
   */
  constructor() {
    // 渲染模板
    $('.dynamic-header').html(hbs())
    $('.header-info').html(userHbs())
  }

  /**
   *  渲染
   *  @return   {object}  null
   */
  render() {
    const self = this
    // 获取用户名
    fetch('fetchUser', (data) => {
      $('.user-name').text(data)
    })
    // 渲染地区列表
    self.renderAreaList()
    // 调用渲染时间
    self.renderTime()
    // 调用退出系统
    self.exitSystem()
  }

  /**
   *  渲染地区列表
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  renderAreaList() {
    const self = this
    // 渲染地区列表
    fetch('fetchAreaList', (data) => {
      areaNams = data.areaList // 保存起来，用于搜索之后用
      // 最后一个重庆市不要
      areaNams = areaNams.slice(0, areaNams.length - 1)
      $('.area-list').html(areaHbs(areaNams))
    })
    // 事件绑定
    self.bindEvent()
    // 搜索
    self.searchEvent()
  }

  bindEvent() {
 
    let setTime = null
    // 点击地区名显示下拉列表
    $('.area-name').on('click', () => {
      const display = $('.area-list').css('display')
      if(display === 'none'){
        $('.area-list').show()
      }else{
        $('.area-list').hide()
      }
      $('.area-list').html(areaHbs(areaNams))
    })

    // 双击输入框清空文字
    $('.input-area').on('dblclick', (evt) => {
      clearTimeout(setTime)
      const $this = $(evt.target)
      $this[0].value = ''
    })

    // 离开下拉列表隐藏
    $('.area-list').hover((evt) => {
      const $this = $(evt.target)
      $this.fadeIn()
    }, () => {
      $('.area-list').hide()
    })

    // 点击空白处关闭下拉列表框
    $(document).mouseup(function(e){
      let _con = $('.area-name') || $('.area-name') // 设置目标区域
      if(!_con.is(e.target) && _con.has(e.target).length === 0){ // Mark 1
        $('.area-list').fadeOut()
      }
    })

    // 点击地区列表
    $('.area-list').off('click', 'li').on('click', 'li', (evt) => {
      $('.drop-down-list').hide()
      $('.dynamic-county-templates .operator-svg rect').hide()
      // top 排行改变样式
      $('.area-top .nav').eq(0).addClass('active').siblings().removeClass('active')
      const $this = $(evt.target)
      let text = $this.text()
      $('.input-area').val(text)
      // 所有地区名跟着变化
      $('.county-name').text(text)
      // top10标题
      if(text === '重庆市') {
        text = ''
      }
      $('.yu-type').text(text)
      // 获取地区id(用于画地图)
      const mapId = $this.attr('areaId')
      // 用于返回给后端
      const areaId = $this.attr('oldId')
      // 显示地区列表
      $('.area-list').fadeOut()
      // if显示主城 else 显示区县
      if(mapId === '500000'){
        $(MAIN_MODULE).slideDown(800)
        $(COUNTY_MODULE).slideUp(800)
      }else{
        // 调用区县的渲染，要传地区id哦
        county.init(mapId, areaId)
        $(MAIN_MODULE).slideUp(300)
        $(COUNTY_MODULE).slideDown(300)
      }
    })
  }

  /**
   *  搜索
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  searchEvent() {
    const self = this
    // 按下鼠标松开
    $('.input-area').on('keyup', (evt) => {
      let keyCode = evt.keyCode
      // 8是回退
      if(keyCode === 8) {
        return false
      }
      self.renderSearch()
    })
  }

  /**
   *  渲染搜索
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  renderSearch() {
    let text = $.trim($('.input-area').val())
    if(text === '') {
      text = 'all' // 为空搜索全部
    }
    fetch('fetchAreaSearch', {text}, (data) => {
      $('.area-list').html(areaHbs(data.areaList))
      if(data.areaList.length === 0) {
        $('.main-list').html('无匹配结果')
      }else{
        $('.main-list').css('display', 'none')
      }
      // 显示全部
      if(text === 'all') {
        $('.main-list').html('重庆市').css('display', 'block')
      }
      $('.area-list').fadeIn()
    })
  }

  /**
   *  退出系统
   *  @return   {object}  null
   */
  exitSystem() {
    $('.out-login').on('click', () => {
      fetch('fetchLogout', (data) => {
        window.location.href = data
      })
    })
  }

  /**
   *  渲染时间
   *  @return   {object}  null
   */
  renderTime() {
    const self = this
    // 地本时间
    // setInterval(() => {
    //   self.getCurrentDate()
    // }, 1000)

    // 调用服务器时间
    self.getServerDate()
  }

  /**
   *  获取服务器时间
   *  @return   {object}  null
   */
  getServerDate() {
    fetchSocket('fetchTimewebsocket', 1, (data) => {
      let date = JSON.parse(data.data) 
      date = date.result
      const year = date.slice(0, 4)
      const month = date.slice(4, 6)
      const day = date.slice(6, 8)
      const housr = date.slice(8, 10)
      const minutes = date.slice(10, 12)
      let time = `${year}年${month}月${day}日  ${housr}:${minutes}`
      $('.current-time').text(time)
    })
  }

  /**
   *  获取本地时间
   *  @return   {object}  null
   */
  getCurrentDate() {
    const self = this
    const date = new Date()
    const year = date.getFullYear()
    const month = self.formateDate(date.getMonth() + 1) 
    const day = date.getDate()
    const housr = self.formateDate(date.getHours())
    const minutes = self.formateDate(date.getMinutes())
    let time = `${year}年${month}月${day}日  ${housr}:${minutes}`
    $('.current-time').text(time)
  }
 
  /**
   *  格式化时间
   *  @param    {number}  t 时间字符串
   *  @return   {string}  时间格式
   */
  formateDate (t) {
    if (t < 10) {
      return `0${t}`
    }
    return t
  }
}



