/**
 * @Author:      zhanghq
 * @DateTime:    2017-10-18 11:14:45
 * @Description: 全市在网终端、在网人数数据
 * @Last Modified By:   zhanghq
 * @Last Modified Time:    2017-10-18 11:14:45
 */

export default {
  url: '/main/inNetwork',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'inNetwork': {
        'terminal|1-1000000000': 1,
        'controlArea|1-1000000': 1
      },
      'operator|3': [
        {
          'name|+1': ['电信', '移动', '联通'],
          'value|1-1000': 1,
          'rate|10-100': 1
        }
      ],
      'communication|3': [
        {
          'name|+1': ['2G', '3G', '4G'],
          'value|1-1000': 1,
          'rate|10-100': 1
        }
      ],
      'areaClassifyCount|10': [
        {
          'name': '@pick(["企事业单位", "重要景区", "重要商圈", "宗教场所", "交通枢纽", "汽车站"])',
          'value|1-1000': 1
        }
      ]
    }
  }
}



