import $ from 'jquery'
import { fetchInterval } from '@/util/request' 
import hbs from '../../templates/main/controlTaskCout.hbs'
import loader from '@/loader/base.config.js'
// 数字滚动
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'

export default class Bottom {

  /**
   *  构造函数
   *  @return   {object}  null
   */
  constructor() {
    this.interval = null
  }
  /**
   *  渲染各模块组件
   *  @return   {object}  null
   */
  render() {
    // 10分钟更新数据 请求管控任务数量统计数据
    fetchInterval('fetchControlTaskCout', (data) => {
      let controlData = data.controlTaskCout
      $('.pages-bottom').html(hbs(controlData))
      setWidth('.warning-number', controlData.warningNumber, 45)
      this.digitRoll(controlData)
    }, loader.updateTime)
  }
  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {object}  null
   */
  digitRoll(data) {
    
    // 范围(10分钟更新一次，20秒刷新一次)
    let range = loader.updateTime / loader.aniTime 
    // 预警次数
    let warning = data.warningNumber - range * 3
    let warningRoll = new DigitRoll({
      container:'.warning-number .num-roll'
    })
    warningRoll.roll(warning)
    // 清除定时切换
    clearInterval(this.interval)
    let t = 0 // 统计setInterval的次数
    // 定时切换
    this.interval = setInterval(function () {
      t++
      // 生成1-3随机数
      let random = randomNumber(1, 3) 
      warning += random
      // 提前加到真实值后就保留真实值，到最后一次了还没加到真实值，就直接跳到真实值
      if(warning > data.warning || t === range) {
        warning = data.terminal
      }
      warningRoll.roll(warning)
    }, loader.aniTime) 
  }
}



