export default {
  url: '/county/inNetwork/:areaId',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'inNetwork|1000-200000': 1, 
      'operator|3': [
        {
          'name|+1': ['电信', '移动', '联通'],
          'value|1-10000000': 1,
          'rate|10-100': 1
        }
      ],
      'communication|3': [
        {
          'name|+1': ['2G', '3G', '4G'],
          'value|1-1000': 1,
          'rate|10-100': 1
        }
      ]
    }
  }
}



